.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    height: 100%;
    padding-top: 70px;
    padding-bottom: 40px;
}

.content {
    padding-top: 5px;
    width: 100%;
    height: 100%;
}

.header {
    color: #8691a2;
    padding: 0 15px 10px;
    border-bottom: 1px solid rgba(134, 145, 162, 0.3);
    margin-bottom: 30px;
    font-size: 25px;
    font-weight: 300;
    width: 800px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.list-wrapper {
    margin-top: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.add-card {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*align-items: center;*/
    margin-right: 25px;
    margin-left: 25px;
    margin-bottom: 50px;
    width: 300px;
    height: 350px;
    /*padding: 5px;*/
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    background-color: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1)
}

.add-card:hover {
    -webkit-box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1)
}

.card-header {
    font-size: 16px;
    font-weight: 500;
    display: -ms-flexbox;
    display: flex;
    margin-top: 50px;
    -ms-flex-pack: center;
        justify-content: center;
}

.icon-wraper {
    font-size: 16px;
    font-weight: 500;
    display: -ms-flexbox;
    display: flex;
    margin-top: 30px;
    -ms-flex-pack: center;
        justify-content: center;
}



