.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*align-items: center;*/
    margin-right: 25px;
    margin-left: 25px;
    margin-bottom: 50px;
    width: 300px;
    height: 350px;
    /*padding: 5px;*/
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    background-color: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1)
}

.container:hover {
    -webkit-box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1)
}
.picture-block {
    width: 100%;
}
.container:nth-of-type(odd) {
    background-color: #fff;
}

.picture {
    width: 100%;
    max-height: 150px;
    /*height: 40px;*/
    display: block;
    border-radius: 5px 5px 0px 0px;
}

.title {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
    border-bottom: 1px solid #f0f8ff;
}

.document-title-wrapper {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
    border-bottom: 1px solid #f0f8ff;
}

.document-title-capture {
    color: rgba(156, 166, 179, 0.8);
    margin-right: 7px;
}

.trainings-label {
    font-weight: 400 !important;
    font-size: 14px !important;
}

.training-info {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
}

.sugestion {
    font-size: 13px;
    font-weight: 200;
}



