.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    height: 100%;
    padding-top: 70px;
    padding-bottom: 40px;
}

.loading-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
}

.list-wrapper {
    margin-top: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
}

.content-header {
    font-size: 20px;
    margin-bottom: 10px;
    -ms-flex-item-align: center;
        align-self: center;
}

.content-block {
    width: 20%;
    min-width: 200px;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
    -webkit-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
    max-height: calc(95vh - 65px);
    overflow: auto;
}

.text-block {
    width: 80%;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
    padding: 20px;
    margin-right: 20px;
    -webkit-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
    max-height: calc(95vh - 65px);
    overflow: auto;
}

.document-info {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    background-color: #fff;
}

.content {
    display: -ms-flexbox;
    display: flex;
    /*width: 100%;*/
    -ms-flex-direction: column;
        flex-direction: column;
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    margin-left: 10px;
}

.container:hover {
    -webkit-box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1)
}

.card-picture-block {
    width: 100%;
}

.card-picture {
    width: 100%;
    /*height: 40px;*/
    display: block;
}

.card-name {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
    border-bottom: 1px solid #f0f8ff;
}

.card-document-description-wrapper {
    /*width: 35%;*/
    color: #37475f;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}



