    -ms-flex-direction: row;
        flex-direction: row;
}
.form-block {
    width: 400px;
    height: 100%;
}

.users-block {
    width: 400px;
    margin-left: 10px;
}
.user-block-title {
    margin-bottom: 5px;
    color: #8691a2;
    font-weight: 600;
    font-size: 14px;
}

.user-block-content {
    background-color: #FFF;
    padding: 5px;
    border: 1px solid #dedede;
    border-radius: 5px;
