    margin-bottom: 20px;
}

.date-input {
    /*margin-bottom: 20px;*/
}

.name-input input {
    color: rgba(0, 0, 0, 0.6) !important;
}

.description-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.groups-input {
    margin-top: 10px;
    width: 100%;
}

.create-btn {
    margin: 30px auto 0 !important;
    display: block !important;
    font-weight: 400 !important;
}



